export function initService() {
	return {
		customer_id: '',
		customer_name: 'customer_name',
		social_credit_code: '123456789123456789',
		contact_name: 'contact_name',
		industry_type_id: 'industry_type_id',
		industry_subtype_id: 'industry_subtype_id',
		province_id: '110000',
		city_id: '110100',
		admin_id: '',
		admin_phone: '15880152020',
		admin_password: '123456',
		power_customer_no: '',
		water_customer_no: '',
		gas_customer_no: '',
		comm_customer_no: '',
		business_licence_img: '',
		start_time: '',
		end_time: '',
		enabled_services: ['1'],
		enabled_industries: ['1']
	}
};
// 表单验证
// 密码验证6-8
function validatePassword(rule,value,callback) {
	let l = value.length,max = 8, min = 6
	if (l<6 || l>8) {
		callback(new Error(`输入${min}-${max}位密码`))
	}else{
		callback()
	}
}
// 各编号20位
function validateCustomerNo (rule,value,callback) {
	if (!value) {
		callback()
	}else if(value.toString().length != 20){
		callback(new Error('输入18位编号'))
	}
}

export function initRule() {
	return {
		customer_name: [
			{required: true, message: '必填字段'},
			{ min: 2, max: 50, message: '长度在 2 到 50 个字符' }
		],
		social_credit_code: [
			{required: true, message: '必填字段'},
			{ min: 18, max: 18, message: '长度为18位' , trigger: 'blur'}
		],
		contact_name: [
			{required: true, message: '必填字段'},
			{ min: 2, max: 20, message: '长度在 2 到 20 个字符' }
		],
		industry_type_id: [
			{required: true, message: '必填字段'}
		],
		industry_subtype_id: [
			{required: true, message: '必填字段'}
		],
		province_id: [
			// {required: true, message: '必填字段'}
		],
		city_id: [
			// {required: true, message: '必填字段'}
		],
		admin_phone: [
			{required: true, message: '必填字段'}
		],
		admin_password: [
			{required: true, message: '必填字段'},
			{validator: validatePassword,trigger: 'blur'}
		],
		power_customer_no: [
			{validator: validateCustomerNo,trigger: 'blur'}
		],
		water_customer_no: [
			{validator: validateCustomerNo,trigger: 'blur'}
		],
		gas_customer_no: [
			{validator: validateCustomerNo,trigger: 'blur'}
		],
		comm_customer_no: [
			{validator: validateCustomerNo,trigger: 'blur'}
		],
	}
};




