<template>
    <div class="service-edit">
    	<el-form ref="form" :model="service" :rules="rules" label-width="110px" class="w800">
    		<div class="w550">
    			<h3>基本设置</h3>
    			<el-form-item label="企业名称:" prop="customer_name">
    				<el-input v-model="service.customer_name" placeholder="请输入企业名称"></el-input>
    			</el-form-item>
    			<el-form-item label="社会信用代码:" prop="social_credit_code">
    				<el-input v-model="service.social_credit_code" placeholder="18位字符"></el-input>
    			</el-form-item>
    			<el-form-item label="联系人:" prop="contact_name">
    				<el-input v-model="service.contact_name" placeholder="请输入企业联系人的真实姓名"></el-input>
    			</el-form-item>
    			<el-form-item label="行业类型:" class="is-required">
    				<el-row>
					    <el-col :span="12" class="pr10">
                            <el-form-item label-width="0" prop="industry_type_id">
    					    	<el-select v-model="service.industry_type_id" placeholder="请选择行业大类">
    					    		<el-option label="行业1" value="行业1"></el-option>
    					    		<el-option label="行业2" value="行业2"></el-option>
    					    	</el-select>
                            </el-form-item>
					    </el-col>
					    <el-col :span="12" class="pl10">
                            <el-form-item label-width="0" prop="industry_subtype_id">
    					    	<el-select v-model="service.industry_subtype_id" placeholder="请选择行业子类">
    					    		<el-option label="行业子类1" value="行业子类1"></el-option>
    					    		<el-option label="行业子类2" value="行业子类2"></el-option>
    					    	</el-select>
                            </el-form-item>
					    </el-col>
					</el-row>
    			</el-form-item>
    			<el-form-item label="所在地:">
    				<!-- todo 城市选择控件未确定 -->
    				<el-row>
					    <el-col :span="12" class="pr10">
                            <!-- <el-form-item label-width="0" prop="province_id"> -->
    					    	<el-select v-model="service.province_id" placeholder="请选择省份" @change="handlerProvinceChange">
    					    		<el-option v-for="(item,i) in provinceList" :key="i" :label="item.org_name" :value="item.org_code"></el-option>
    					    	</el-select>
                            <!-- </el-form-item> -->
					    </el-col>
					    <el-col :span="12" class="pl10">
                            <!-- <el-form-item label-width="0" prop="city_id"> -->
    					    	<el-select v-model="service.city_id" placeholder="请选择城市">
                                <el-option v-for="(item,i) in cityList" :key="i" :label="item.org_name" :value="item.org_code"></el-option>
    					    	</el-select>
                            <!-- </el-form-item> -->
					    </el-col>
					</el-row>
    			</el-form-item>
    			<el-form-item label="管理员帐号:" prop="admin_phone">
    				<el-input v-model="service.admin_phone" placeholder="请输入手机号作为帐号"></el-input>
    			</el-form-item>
    			<el-form-item label="设置密码:" prop="admin_password">
    				<el-input v-model="service.admin_password" type="password" placeholder="设置密码"></el-input>
    			</el-form-item>
    			<el-form-item label="电力用户编码:" prop="power_customer_no">
    				<el-input v-model="service.power_customer_no" placeholder="填写电力用户编码，为客户更好提供服务"></el-input>
    			</el-form-item>
    			<el-form-item label="水力用户编码:" prop="water_customer_no">
    				<el-input v-model="service.water_customer_no" placeholder="填写水力用户编码，为客户更好提供服务"></el-input>
    			</el-form-item>
    			<el-form-item label="燃气用户编码:" prop="gas_customer_no">
    				<el-input v-model="service.gas_customer_no" placeholder="填写燃气用户编码，为客户更好提供服务"></el-input>
    			</el-form-item>
    			<el-form-item label="通信服务编码:" prop="comm_customer_no">
    				<el-input v-model="service.comm_customer_no" placeholder="填写通信服务编码，为客户更好提供服务"></el-input>
    			</el-form-item>
    		</div>
			<el-form-item label="营业执照:" class="bb">
                <upload-image 
                    :config="uploadConfig" 
                    :file-list="fileList"
                    @on-success="uploadSuccessHandler"></upload-image>


			</el-form-item>
    		<div class="w550">
				<h3>服务设置</h3>
    			<el-form-item label="有效时间:">
    				<el-date-picker v-model="dateRange" type="daterange" id="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" @change="changeDageRange">
		            </el-date-picker>
    			</el-form-item>
    			<el-form-item label="开启服务:">
    				<el-checkbox-group v-model="service.enabled_services">
    				    <el-checkbox label="资产管理">资产管理</el-checkbox>
    				    <el-checkbox label="运维管理">运维管理</el-checkbox>
    				</el-checkbox-group>
    			</el-form-item>
    			<el-form-item label="开启行业:">
    				<el-checkbox-group v-model="service.enabled_industries">
	    				<el-checkbox label="电力行业">电力行业</el-checkbox>
    				</el-checkbox-group>
    			</el-form-item>

    		</div>
    		<div class="w550 tc">
    			<el-button type="primary" @click="doApply">申请</el-button>
    			<el-button @click="doCancel">取消</el-button>
    		</div>

    	</el-form>
    </div>
</template>
<script>
import areaMixin from '@/mixins/areaMixin'
import uploadImage from '@/components/upload-image'
import * as constants from './constants.js'
import {fmt} from '@/utils/util'
export default {
    data() {
        return {
            service: constants.initService(),
        	rules: constants.initRule.call(this),
        	dateRange: '',// 时间范围
            uploadConfig: {
                accept: 'image/png, image/jpg',
                noteMessage: '请提供证件的原件照片或彩色扫描件，文字/盖章清晰可辨认，jpg、jpeg、pngges ，不超过10MB'
            },
            fileList: [],// 图片上传列表
            

        }
    },
    components: {
        uploadImage
    },
    mixins: [areaMixin],
    computed: {
    	id (n,o){
    		return this.$route.query.id
    	}
    },
    methods: {
    	loadServiceDetail (){
    		// this.$api.service.getServiceById(this.id).then((data) => {
    		// 	this.service = data
                // if (this.service.province_id) {
                //     this.getCityList(this.service.province_id)
                // }   
    		// })
    	},
    	doApply (){
    		this.$refs.form.validate((valid)=>{
    			if (valid) {
                    // console.log(this.service.start_time,this.service.end_time)
    				let service = Object.assign({}, this.service)
                    service.start_time = fmt.getTime(service.start_time)
                    service.end_time = fmt.getTime(service.end_time)
                    service.business_licence_img = this.fileList[0] ? this.fileList[0].url : ''
                    if (this.id) {
                        // 编辑
                    }else{
                        // 新增
                        this.$api.service.addService(service).then((data) => {
                            this.$message('添加成功')
                            this.$refs.form.resetFields()
                        })
                    }
    				
    			}
    		})
    	},
		doCancel () {
			console.log('cancel apply')
			this.$refs.form.resetFields()
		},
		changeDageRange(e){
    		this.service.start_time = e ? e[0] || '' : ''
    		this.service.end_time = e ? e[1] || '' : ''
    	},
        uploadSuccessHandler(rsp,file,fileList,vm){
            this.fileList = [file]
            console.log(this.fileList)
        }
    },
    created(){
    	if (this.id) {
    		this.loadServiceDetail()
    	}
        if (this.service.province_id) {
            this.getCityList(this.service.province_id)
        }
    }
}
</script>
<style lang="scss">
	.service-edit{
		.el-select{
			width: 100%;
		}
		.w800{
			width: 800px;
		}
	}

	
	
</style>


